	module.exports = {"data":{"post":{"html":"<p>All week long we've been hearing stories about the chaos caused by the new 'practice fee' <em>(Praxisgeb端hr)</em> for health care introduced at the beginning of the year. Applied to those with public health insurance (90% of all Germans), 10 Euros is to be paid for each doctor visited per quarter in cash at the time of the first visit. The fee is waived for children and for referrals. The idea is to discourage patients from going directly to specialists without a referral from their family physician. Although the fee is collected by physicians, it goes entirely to the insurance funds. The physicians bear the administrative costs and the risk for those patients they treat but who refuse to pay.</p>\n<p>But the devil is in the details. Germans in the public insurance system have never seen a bill for their health care... now they suddenly have to pay before they see a doctor. The fee also applies to emergency rooms and ambulances (unless you called the ambulance through 110), so emergency personnel need to carry cash boxes and issue receipts. (The fee also applies to those given care involuntarily... imagine trying to collect cash from a drunk being admitted to detox.) The fee applies to welfare recipients and pensioners. There is a cap of 2% of annual income (1% for the 'chronically ill', which the law doesn't really define) for all practice fees and co-payments... so make sure you save all your receipts.</p>\n<p>The fee was part of the bi-partisan health reform law of last year, so both major parties are to blame. Best of all, the members of Bundestag <a title=\"Praxisgeb端hren-Rabatt: Die Parlamentarier haben verstanden - Wirtschaft - SPIEGEL ONLINE\" href=\"http://www.spiegel.de/wirtschaft/0,1518,281184,00.html\">exempted themselves from the fee</a>, along with civil servents. Instead of 10 Euros per doctor per quarter, they pay a flat 20 Euros per year.</p>\n<p>My guess is the practice fee will quickly be repealed.</p>","fields":{"title":"Practice Fee","date":"09 Jan 2004","url":"https://papascott.de/2004-01-09-practice-fee/"}}},"pathContext":{"slug":"/2004-01-09-practice-fee/","prev":{"fields":{"title":"ARD Tagesschau: News in English","date":"07 Jan 2004","slug":"/2004-01-07-ard-tagesschau-news-in-english/"}},"next":{"fields":{"title":"Flip the little Dolphin","date":"10 Jan 2004","slug":"/2004-01-10-flip-the-little-dolphin/"}}}}

